/* eslint-disable */
import React, { useState, useEffect, useRef } from "react"
import { connect } from "react-redux"
import { Button } from 'antd'
import Message from "./Message"
import NewMessageField from "./NewMessageField"
import { openDialog } from "../actions/dialogs"
import socket from "../transport/Connect"
import axios from 'axios'
import { ui } from "../reducers/ui"
import helpers from "../helpers"
import Crypto from "y_crypto"

function Dialog(props) {
  const [messages, setMessages] = useState([])
  const [dialog, setDialog] = useState(null)
  const [messagesLoading, setMessagesLoading] = useState(false)
  const [height1, setHeight1] = useState(0)
  const [height2, setHeight2] = useState(0)
  const mesViewport = useRef(null)

  // функция для получения отправитля в сообщениях и в пересланных сообщениях
  const getUsers = mess => {
    const users = []
    mess.forEach(message => {
      if (message.Attachments) {
        if (message.Attachments.length) {
          if (message.Attachments[0].Type === 5) {
            if (message.Attachments[0].Payload.length > 0) {
              const foundUser = props.users.find(
                user => user.Id === message.Attachments[0].Payload[0].SenderId
              )

              if (!foundUser) {
                if(message.Attachments[0].Payload[0].SenderId){
                  users.push(message.Attachments[0].Payload[0].SenderId)
                }
              }
            }
          }
        }
      }
      const foundUser = props.users.find(user => user.Id === message.SenderId)

      if (!foundUser) {
        if(message.SenderId){
          users.push(message.SenderId)
        }
      }
    })
    const uniqueUsers = [...new Set(users.map(n => n))]

    if (uniqueUsers.length) {
      const requestUsers = {
        UsersId: [...uniqueUsers],
        RequestType: 28,
        RequestId: socket.makeId(),
        Type: 0
      }

      socket.send(requestUsers)
    }
  }

  useEffect(() => {
      if (localStorage.getItem("keysPair")) {
        localStorage.setItem("keysUpload", true)
        const addNewKeys = {
          Keys: [
            {
              KeyId: socket.makeId(),
              Data: JSON.parse(localStorage.getItem('keysPair')).public, 
              Lifetime: 99999,
              GenerationTime: parseInt(new Date().getTime()),
              Version: 1,
              UserId: props.user.Id,
              ChatId: null
            }
          ],
          RequestType: 100,
          RequestId: socket.makeId(),
          Type: 0
        }

        socket.send(addNewKeys)
    }
  }, [props.user, props.dialog])

  useEffect(() => {
    if(props.dialog){
      setDialog(props.dialog)

      // отмечаем сообщения прочитанными
      if (props.appFocus) {
        if (props.dialog.LastMessageId) {
          const data = {
            MessagesId: [props.dialog.LastMessageId],
            ConversationType: props.dialog.ConversationType,
            ConversationId: props.dialog.ConversationId,
            RequestId: socket.makeId(),
            RequestType: 35,
            Type: 0
          }
  
          socket.send(data)
        }
      }

      // подгрузка сообщений по скроллу
      mesViewport.current.onscroll = () => {
        if (mesViewport.current.scrollTop === 0) {
          setHeight1(mesViewport.current.scrollHeight)
          setMessagesLoading(true)

          const messagesRequest = {
            ConversationType: props.dialog.ConversationType,
            ConversationId: props.dialog.ConversationId,
            NavigationMessageId: messages[0].GlobalId,
            MessagesId: null,
            RequestType: 25,
            RequestId: socket.makeId(),
            Direction: true,
            Type: 0
          }

          socket.send(messagesRequest)
        }
      }
    }
    // setHeight2(mesViewport.current.scrollHeight)
  }, [messages, props.dialog])

  useEffect(() => {
    // console.log(height2)
  }, [height1, height2])

  useEffect(() => {
    if (dialog) {
      const mess = props.messages.filter(message => message.ConversationId === parseInt(dialog.ConversationId || dialog.ChannelId) && message.ConversationType === dialog.ConversationType)
      setMessages(mess)

      setHeight2(mesViewport.current.scrollHeight)
      setTimeout(() => {
        if (mesViewport.current && !messagesLoading) {
          mesViewport.current.scrollTop = 450000
        } else {
          mesViewport.current.scrollBottom = height1 - height2
        }
      }, 10)

      getUsers(mess)
    }
    setHeight2(mesViewport.current.scrollHeight)
  }, [dialog, props.messages])







  useEffect(() => {
    if (props.isCrypto) {
      // получение ключей шифрования
      if (props.user && props.dialog) {
        const getUserKeys = {
          UserId: props.dialog.SecondUid,
          Direction: true,
          KeysId: null,
          RequestType: 105,
          NavigationTime: parseInt(new Date().getTime()),
          RequestId: socket.makeId(),
          Type: 0
        }

        socket.send(getUserKeys)
      }
    }
  }, [props.isCrypto])

  useEffect(() => {
    // отправляем симметричный ключ собеседнику
    if (props.symmetricKey && !localStorage.getItem("keysUpload")) {
      console.log('Отправляем симметричный ключ')
      const keyExchangeMessage = {
        Messages: [
          {
            Id: 0,
            SendingTime: "",
            SenderId: props.user.Id,
            ReceiverId: props.dialog.SecondUid,
            ConversationId: props.dialog.ConversationId,
            ConversationType: props.dialog.ConversationType,
            Read: false,
            NodesId: null,
            ReplyTo: null,
            Text: null,
            Attachments: [
              {
                Type: 6,
                Hash: null,
                MessageId: 0,
                Payload: {
                  KeyId: props.symmetricKey.id,
                  SignKeyId: 1,
                  EncryptedData: props.symmetricKey.key
                },
                NodesId: null
              }
            ],
            GlobalId: null
          }
        ],
        RequestType: 39,
        RequestId: socket.makeId(),
        Type: 0
      }
  
      socket.send(keyExchangeMessage)
    }
  }, [props.symmetricKey])

  const joinToChannel = () => {
    const data = {
      ChannelsId: [dialog.ChannelId],
      UsersId: [props.user.Id],
      RequestType: 48,
      RequestId: socket.makeId(),
      Type: 0
    }

    socket.send(data)
  }

  const fileOver = e => {
    e.preventDefault()
    mesViewport.current.classList.add('file-over')
  }

  const fileLeave = e => {
    e.preventDefault()
    mesViewport.current.classList.remove('file-over')
  }

  const fileDrop = (e) => {
    e.preventDefault()
    mesViewport.current.classList.remove('file-over')

    if (props.isCrypto) {
      // зашифрованное сообщение с файлом
      const encoder = new TextEncoder()
      const masterPassword = encoder.encode('qwerty')
      let masterPasswordId = BigInt(1)
      const crypto = new Crypto(masterPassword, masterPasswordId)

      crypto.setSymmetricKey(helpers.base64ToBuffer(props.symmetricKey.key))
      crypto.encryptMsg(1, 1, 99999, encoder.encode(e.dataTransfer.files[0])).then(data => {
        if (data.length > 0) {
          const fileAccessToken = localStorage.getItem('FileAccessToken')
          axios.defaults.headers['FileAccessToken'] = fileAccessToken

          const formData = new FormData()
          formData.append('file', new Blob(data))

          axios.post(`https://${props.node}:5000/api/Files`, formData).then(upladedFile => {
            crypto.encryptMsg(1, 4, 99999, encoder.encode(upladedFile.data.File.FileId)).then(encryptImgMsg => {
              const data = {
                Messages: [
                  {
                    Id: 0,
                    SendingTime: "",
                    SenderId: props.user.Id,
                    ReceiverId: dialog.SecondUid,
                    ConversationId: dialog.ConversationId,
                    ConversationType: dialog.ConversationType,
                    Read: false,
                    NodesId: null,
                    ReplyTo: null,
                    Text: null,
                    Attachments: [
                      {
                        Type: 4,
                        Hash: null,
                        MessageId: 0,
                        Payload: {
                          KeyId: props.symmetricKey.id,
                          SignKeyId: null,
                          SaveFlag: 1,
                          Lifetime: 99999,
                          EncryptedData: helpers.bufferToBase64(encryptImgMsg)
                        },
                        NodesId: null
                      }
                    ],
                    GlobalId: null
                  }
                ],
                RequestType: 39,
                RequestId: socket.makeId(),
                Type: 0
              }
      
              socket.send(data)
            }).catch(err => {
              console.log(err)
            })
          }).catch(err => {
            console.log(err)
          })
        }
      }).catch(err => {
        console.log(err)
      })
    } else {
      // обычное сообщение с файлом
      const isJpgOrPng = e.dataTransfer.files[0].type === "image/jpeg" || e.dataTransfer.files[0].type === "image/png"
      let attachmentType = null
      if (isJpgOrPng) {
        attachmentType = 2
      } else {
        attachmentType = 1
      }

      const fileAccessToken = localStorage.getItem('FileAccessToken')
      axios.defaults.headers['FileAccessToken'] = fileAccessToken

      const formData = new FormData()
      formData.append('file', e.dataTransfer.files[0])

      axios.post(`https://${props.node}:5000/api/Files`, formData)
      .then(
          (res) => {
            if (res.status === 200) {
              // setImgLoading(false)
              const data = {
                Messages: [
                  {
                    Id: 0,
                    SendingTime: "",
                    SenderId: props.user.Id,
                    ReceiverId: dialog.SecondUid,
                    ConversationId: dialog.ConversationId,
                    ConversationType: dialog.ConversationType,
                    Read: false,
                    NodesId: null,
                    ReplyTo: null,
                    Text: "",
                    Attachments: [
                      {
                        Type: attachmentType,
                        Hash: null,
                        MessageId: 0,
                        Payload: res.data.File.FileId,
                        NodesId: null
                      }
                    ],
                    GlobalId: null
                  }
                ],
                RequestType: 39,
                RequestId: socket.makeId(),
                Type: 0
              }
        
              socket.send(data)
            }
          }
      )
      .catch(
          (err) => {
            console.log(err)
          }
      )
    }
  }

  return (
    <div className="dialog">
      <div 
        className="dialog__messages-viewport"
        ref={mesViewport}
        onDragOver={(e) => fileOver(e)}
        onDragLeave={(e) => fileLeave(e)}
        onDrop={(e) => fileDrop(e)}
      >
        <div className="dialog__messages">
          {dialog
            ? messages.map(message => {
                return <Message key={socket.makeId()} message={message} />
              })
            : null}
        </div>
      </div>
      {props.forwardMessage ? (
        <div className="reply-message">
          Ответ на:
          {` ${props.forwardMessage.Text}`}
        </div>
      ) : null}
      {
        props.dialog ? 
          props.dialog.type === "channel" && props.dialog.fake ? 
            <Button type="primary" size='large' onClick={joinToChannel}>Вступить</Button> 
          : props.dialog.ConversationType !== 3 ?
            <NewMessageField {...props} dialog={dialog} />
          : null
        : null
      }
    </div>
  )
}

function mapStateToProps(state) {
  // console.log(state)
  return {
    messages: state.messages,
    forwardMessage: state.forwardMessage,
    users: state.users,
    user: state.users.find(user => user.currentUser === true),
    node: state.node.activeNode.Domains[0],
    appFocus: state.ui.appFocus,
    requestsId: state.ui.requestsId,
    symmetricKey: state.symmetricKey,
    isCrypto: state.ui.isCrypto
  }
}

const mapDispatchToProps = dispatch => {
  return {
    openDialog: dialogId => dispatch(openDialog(dialogId))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dialog)
